<script>
export default {
  name: 'Card',
  props: {
    cardData: {
      type: Object,
      required: true
    },
    isPlayerCard: Boolean,
    selected: Boolean,
    dying: Boolean
  },
  computed: {
    visible() {
      return this.$props.cardData.visible || this.$props.isPlayerCard;
    }
  }
};
</script>

<style scoped lang='scss'>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.skull {
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: center;
  transform: translate(-50%, -50%) scale(0.15);
  width: 510px;
  height: 490px;
  filter: drop-shadow(3px 5px 20px #00000091) brightness(85%);
}

.card {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  --size: 8rem;
  width: var(--size);
  height: calc(var(--size) * 1.4);
  position: relative;
  margin: 1rem;
  border-radius: 0.15rem;
  background-color: hsl(0, 0%, 98%);
  transition: transform 0.2s, background-color 0.2s, width 0.2s 0.3s,
    margin 0.2s 0.3s;
  cursor: pointer;

  &.dying {
    width: 1px;
    background-color: transparent;
    margin-left: 0;
    margin-right: 0;

    .attack,
    .defense,
    .health {
      opacity: 0;
    }
  }

  .card-img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
  &.visible {
    background-color: hsl(0, 0%, 55%);
    .card-img {
      transition: filter 0.2s;
      filter: brightness(80%);
    }
    &:hover,
    &.selected {
      background-color: hsl(0, 0%, 98%);
      .card-img {
        filter: brightness(100%);
      }
    }
  }

  &:hover,
  &.selected {
    transform: translateY(1.5rem);

    &.isPlayer {
      transform: translateY(-1.5rem);
    }
  }

  .attack,
  .defense,
  .health {
    position: absolute;
    margin: 0;
    width: 1.5rem;
    height: 1.5rem;
    p {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      margin: 0;
      font-size: 0.8rem;
      font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
        Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }

    .background-icon {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 2rem;
      height: 2rem;
    }
  }

  --dist: 0.65rem;
  .attack {
    top: var(--dist);
    left: var(--dist);
  }
  .defense {
    top: var(--dist);
    right: var(--dist);
  }
  .health {
    bottom: var(--dist);
    left: var(--dist);
  }
  .name {
    position: absolute;
    width: 100%;
    text-align: center;
    top: -2rem;
    margin: 0;
    opacity: 0;
    transition: opacity 0.2s;
    text-overflow: ellipsis;
    overflow: hidden;
    display: block;
    white-space: nowrap;
  }
  &.visible {
    &:hover .name,
    &.selected .name {
      opacity: 1;
    }
  }
  &.isPlayer .name {
    top: unset;
    bottom: -2rem;

    &:hover .name,
    &.selected .name {
      opacity: 1;
    }
  }
}
</style>